import React from "react"
import { graphql } from "gatsby"
import styled from "styled-components"
import posed from "react-pose"
import Logo from "../components/logo"
import Img from "gatsby-image"


const Transition = posed.div({
  enter: {
    opacity: 1,
    transition: {
      duration: 600,
      ease: 'easeInOut'
      }
  },
    exit: { 
      opacity: 0,
    transition: {
      duration: 600,
      ease: 'easeInOut'
      },
      
  },
})

const Information = styled.div`
  background-color: ${props => props.color};
  font-family: 'Calibre-Regular';
  color: white;
  height: 100%;
  position: relative;
  z-index: 990;

  @media(min-width: 992px) {
    height: 100%;
  }
`
const Title = styled.h1`
  font-size: 48px;
  margin-left: 25px;
  margin-right: 25px;
  padding-top: 25px;
`

const Text = styled.p`
  font-size: 24px;
  margin-left: 25px;
  margin-right: 25px;
  padding-top: 25px;
  max-width: 800px;
`

const Page = ({ data: { prismicPage } }) => {

  const { data } = prismicPage;

  var itemsPre = function(prismicPage) {
    this.name = prismicPage.data.title.text;
    this.tags = [];
    prismicPage.tags.map(tag => {return this.tags.push(tag)});
  }

  var items = new itemsPre(prismicPage);

  console.log(items);
  console.log(data.image_gallery);

  return (
    <Transition>
      <div>
      {data.image_gallery.map((image, index) => (
        <div key={index}>
          <Img  style={{width: '100%'}} fluid={image.image.localFile.childImageSharp.fluid} />
        </div>
        )
      )}
      </div>
      <Logo/>
      <Information color={`${prismicPage.data.color}`}>
        <Title>{items.name}</Title>
        <Text>{data.text.text}</Text>
      </Information>
    </Transition>
  )
}

export default Page

export const pageQuery = graphql`
  query PostBySlug($uid: String!) {
    prismicPage(uid: { eq: $uid }) {
      tags
      uid
      data {
        title {
          text
        }
        text {
          text
        }
        color
        image_gallery {
          image {
            alt
            copyright
            url
            localFile {
              childImageSharp {
                fluid(maxWidth: 1500) {
                  ...GatsbyImageSharpFluid_withWebp
                }
              }
            }
          }
        }
      }
    }
  }
`
